import { Icon as Iconify } from "@iconify/react";
import { Alert, Card, CardContent, Grid, styled } from "@mui/material";
import DOMPurify from "dompurify";
import Markdown from "marked-react";

import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import Form from "./Form";

const MarkdownContainer = styled("div")(({ theme }) => ({
	"& h3": {
		marginTop: 0,
	},
}));

const Page = () => {
	const GUIDE = `
### نکات آموزشی

اگر شما هر بار که مشتری به شما مراجعه می کند از او میخواهید که شماره موبایل خود را وارد دستگاه کنید می توانید از افزونه هدفگیری مجدد برای افزایش فروش خود کمک بگیرید.

کار این افزونه این است که به آن دسته از مشتریان شما که مدت مشخصی است به فروشگاه شما مراجعه نکرده اند یک پیامک ارسال می کند ، شما با یک متن جذاب می توانید مشتری را مجددا برای خرید از فروشگاه ترغیب کنید.

به عنوان مثال اگر می خواهید هر مشتری که 3 ماه به شما مراجعه نکرد یک پیامک از شما دریافت کند کافی است در قسمت متن پیامک ، متن مورد نظر خود را بنویسید و در قسمت زمان ارسال پیامک در بخش "اگر مشتری ... روز به شما مراجعه نکرد..." عدد 90 را وارد کنید. در این مثال به هر مشتری که 90 روز به شما مراجعه نکند به صورت خودکار متن تعریف شده شما پیامک می شود.
`;

	return (
		<TabView
			title="هدفگیری مجدد"
			Icon={(props) => (
				<Iconify {...props} icon="solar:gps-bold-duotone" />
			)}
		>
			<ResponsiveContainer>
				<Card variant="outlined" sx={{ mb: 3 }}>
					<CardContent sx={{ p: 3 }}>
						<Grid container spacing={5}>
							<Grid item xs={12} sm={6}>
								<Alert color="info">
									<MarkdownContainer>
										<Markdown breaks={true}>
											{DOMPurify.sanitize(GUIDE)}
										</Markdown>
									</MarkdownContainer>
								</Alert>
							</Grid>
							<Grid item xs={12} md={6}>
								<Form />
							</Grid>
						</Grid>
					</CardContent>
				</Card>
			</ResponsiveContainer>
		</TabView>
	);
};

export default Page;
